nav {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(255,213,25);
    padding: 0.8rem 0.5rem;
    height: 3.5rem;
    transition: 1s ease-in-out;
}

.left-side {
    width: 100%;
}


nav #close .hidden-links{
    display: none;
}

nav #close .logo-nav{
    display: flex;
    width: auto;
    height: 46px;
    padding-left: 3rem; 
}

nav #open .hidden-links{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    
}

nav #open .logo-nav {
   display: none;
}

.right-side{
    display: none;
    align-items: center;
    width: 60%;
    justify-content: space-around;
}

.right-side img{
    display: none;
}

.nav-icon {
    height: auto;
    width: 30px; 
    padding: 0 0.1rem;
}

.shoe {
    height: 40px;
    width: auto;
}

.giftbox{
    height: 27px;
    width: auto;
}

button {
    display: flex;
    border: none;
    background-color: rgb(255,213,25);
    
}

@media only screen and (min-width: 600px) {
    nav #close .hidden-links{
        display: none;
    }

    nav #open .hidden-links{
        display: none;
    }

    nav #open .logo-nav {
        display: flex;
        width: auto;
        height: 46px;
        padding-left: 3rem; 
    }

    nav #closed .logo-nav {
        display: flex;
        width: auto;
        height: 46px;
        padding-left: 3rem; 
    }

    .right-side{
        display: flex;
        padding-right: 3rem;
    }

    .right-side img {
        display: flex;
    }

    .logo-nav{
        display: flex;
    }

    button {
        display: none;
    }
}


